@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
:root {
    --main-bg: #ffffff;
    --second-bg: #fafafb;
    --txt-color: #455560;
    --txt-white: #fff;
    --main-color: #349eff;
    --second-color: #62b4ff;
    --box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

    --main-bg-light: #ffffff;
    --second-bg-light: #fafafb;
    --txt-color-light: #455560;
    --box-shadow-light: rgba(149, 157, 165, 0.2) 0px 8px 24px;

    --main-bg-dark: #2d2d2d;
    --second-bg-dark: #202020;
    --txt-color-dark: #bbbbbb;
    --box-shadow-dark: rgba(0, 0, 0, 0.2) 0px 5px 10px;

    --main-color-blue: #349eff;
    --second-color-blue: #62b4ff;

    --main-color-red: #fb0b12;
    --second-color-red: #ff4a6b;

    --main-color-cyan: #10d4d2;
    --second-color-cyan: #2ae9e6;

    --main-color-green: #019707;
    --second-color-green: #4caf50;

    --main-color-orange: #d68102;
    --second-color-orange: #fca11a;
    --box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;

    --sidebar-width: 300px;
    --border-radius: 15px;
    --topnav-height: 110px;
    --transition-cubic: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
body {
    font-family: 'Roboto', sans-serif;
    -webkit-font-smoothing: transparent;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
}
.slider img,
.card-image {
    height: 450px;
    object-fit: cover;
}

.pointer {
    cursor: pointer;
}

.awssld {
    height: 270px;
    border-radius: 10px;
}
.awssld img {
    width: 100%;
}
@media (max-width: 976px) {
    .awssld {
        width: 100vw;
    }
}

/*---Logo---*/
#wheels {
    -webkit-animation: wheels 1s ease infinite;
            animation: wheels 1s ease infinite;
    transform-origin: center;
    transform-box: fill-box;
}

#feet {
}

@-webkit-keyframes wheels {
    form {
        transform: rotateZ(0);
    }
    to {
        transform: rotateZ(360deg);
    }
}

@keyframes wheels {
    form {
        transform: rotateZ(0);
    }
    to {
        transform: rotateZ(360deg);
    }
}
.swiper {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;

    /* Center slide text vertically */
    display: flex;
    justify-content: center;
    align-items: center;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.light-background {
    background-color: #ffffff;
    background-color: var(--main-bg-light);
    color: #000;
}

.dark-background {
    background-color: #2d2d2d;
    background-color: var(--main-bg-dark);
    color: #fff;
}

.blue-color {
    background-color: #349eff;
    background-color: var(--main-color-blue);
    color: #fff;
}

.red-color {
    background-color: #fb0b12;
    background-color: var(--main-color-red);
    color: #fff;
}

.cyan-color {
    background-color: #10d4d2;
    background-color: var(--main-color-cyan);
    color: #fff;
}

.green-color {
    background-color: #019707;
    background-color: var(--main-color-green);
    color: #fff;
}

.orange-color {
    background-color: #d68102;
    background-color: var(--main-color-orange);
    color: #fff;
}
.starcontainer {
    position: relative;
}
.starOutlined {
    position: absolute;
    z-index: -2;
    top: -11px;
    right: -15px;
    fill: rgb(236, 202, 9);
}

/*--------Admin Specifications----------*/
.specifications-modal {
    background-color: #333;
    height: 600px;
    width: 1000px;
    position: absolute;
    z-index: 999;
    top: 20%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 2rem;
    visibility: hidden;
    opacity: 0;
}
.specifications-modal > div {
    display: flex;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    margin-bottom: 1rem;
}
.specifications-modal label {
    width: 120px;
}
.specifications-modal input {
}
.specifications-modal button {
    padding: 0.5rem 2rem;
    border-radius: 5px;
    background-color: #62b4ff;
    color: white;
    cursor: pointer;
    font-weight: 600;
}
.open-admin-spec {
    padding: 0.5rem 2rem;
    border-radius: 5px;
    background-color: #62b4ff;
    color: white;
    font-weight: 600;
    cursor: pointer;
}
.show-spec {
    visibility: visible;
    opacity: 1;
}
.awssld {
    overflow: hidden;
}

.theme-mode-light {
    --main-bg: var(--main-bg-light);
    --second-bg: var(--second-bg-light);
    --txt-color: var(--txt-color-light);
    --box-shadow: var(--box-shadow-light);
}

.theme-mode-dark {
    --main-bg: var(--main-bg-dark);
    --second-bg: var(--second-bg-dark);
    --txt-color: var(--txt-color-dark);
    --box-shadow: var(--box-shadow-dark);
}

.theme-color-blue {
    --main-color: var(--main-color-blue);
    --second-color: var(--second-color-blue);
}

.theme-color-red {
    --main-color: var(--main-color-red);
    --second-color: var(--second-color-red);
}

.theme-color-cyan {
    --main-color: var(--main-color-cyan);
    --second-color: var(--second-color-cyan);
}

.theme-color-green {
    --main-color: var(--main-color-green);
    --second-color: var(--second-color-green);
}

.theme-color-orange {
    --main-color: var(--main-color-orange);
    --second-color: var(--second-color-orange);
}

