}

.dropdown__toggle {
    border: 0;
    outline: 0;
    background-color: transparent;
}
.dropdown__toggle > i {
    font-size: 2.5rem;
    color: var(--text-color);
}

.dropdown__toggle-badge {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: -12px;
    right: -10px;
    height: 25px;
    width: 25px;
    border-radius: 50%;
    background-color: var(--main-color);
    color: var(--txt-color);
